import { UIController } from "./UIController";
import { UIView } from "./UIView";
export class UIManager {
    constructor() {
        const view = new UIView();
        this._controller = new UIController(view);
    }
    showSpin() {
        this._controller.showSpin();
    }
    hideSpin() {
        this._controller.hideSpin();
    }
}
